    bottom: 25px;
    background: orange;
    width: 100%;
    text-align: center;
  }
  .arrowIcon{
    top:-11px;
    left:40px;
  }
  .itemsCarousel .carousel-indicators{
    display:none
  }
  .itemsCarousel .carousel-control-next{
    width:30px;
    height:30px;
    top:-55px;
    background-color: black;
    opacity: 1;
    border-radius: 5px;
  }
  .itemsCarousel .carousel-control-next-icon{
    color:white;
    width:70%;
    filter:unset !important;
  }
  .itemsCarousel .carousel-control-prev{
    width: 30px;
    height: 30px;
    top: -55px;
    left: auto;
    right: 40px;
    background-color: black;
    opacity: 1;
    border-radius: 5px;
  }
  .itemsCarousel .carousel-control-prev-icon{
    color:white;
    width:70%;
    filter:unset !important;
  }
  .itemsCarousel .carousel-item.active{
    display: flex !important;
    flex-wrap: wrap;
  }
  .hideItemAddedToTheCart{
    position:fixed;
    left:-100%;
    bottom:20%;
    padding: 10px;
    background-color: #0d6efd;
    color:white;
    border-radius:10px;
}
.itemAddedToTheCart{
    transition:all 3s;
    position:fixed;
    left:14%;
    bottom:20%;
    padding: 10px;
    background-color: #0d6efd;
    color:white;
    border-radius:10px;
}