.shippingOptionHr , .carsTotalHr{
    opacity: 1;
}
.shippingOptionHr{
    margin-top: 40px;
}
.carsTotalHr{
    margin-bottom: 20px;
}
.shippingOptionHr::before{
    content: "Shiping Option";
    position: absolute;
    color: black;
    transform: translate(-50%,-50%);
    background: white;
    border: solid 1px;
    border-radius: .25rem;
    padding: 3px;
}
.carsTotalHr::before{
    content: "Cart Total";
    position: absolute;
    color: black;
    transform: translate(-50%,-50%);
    background: white;
    border: solid 1px;
    border-radius: .25rem;
    padding: 3px;
}