.hideItemAddedToTheCart{
    position:fixed;
    left:-100%;
    bottom:20%;
    padding: 10px;
    background-color: #0d6efd;
    color:white;
    border-radius:10px;
}
.itemAddedToTheCart{
    transition:all 3s;
    position:fixed;
    left:14%;
    bottom:20%;
    padding: 10px;
    background-color: #0d6efd;
    color:white;
    border-radius:10px;
}