.navbar {
  background-color: midnightblue;
  color: white;
  z-index:1000;
  overflow-x:clip;
}
.burgermenuMenu {
width:75%;
position: absolute;
background-color: lightslategray;
z-index:1000;
top:0px;
left:0px;
transition: 0.5s;
}
.burgermenuMenu ul {
  padding-top:10px;
}
.burgermenuMenu ul span {
  display: flex;
  justify-content: right;
  margin-right: 20px;
  font-weight: bolder;
  cursor: pointer;
}
.burgermenuMenu ul li {
  padding : 10px 10px 10px 20px
}
.burgermenuMenu ul li:hover {
  background-color:darkslategray;
}
.closeMenu {
  width:25%;
  position: absolute;
  background-color: lightslategray;
  z-index:1000;
  top:0px;
  left:-30%;
}
.closeMenu ul {
  padding-top:10px;
}
.closeMenu ul span {
  display: flex;
  justify-content: right;
  margin-right: 20px;
  font-weight: bolder;
  cursor: pointer;
}
.closeMenu ul li {
  padding : 10px 10px 10px 20px
}
.closeMenu ul li:hover {
  background-color:darkslategray;
}
.navbarHead {
  font-size: 12px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.navbarHead ul {
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
.navbarHead ul li {
  display: ruby;
  margin: 0px 5px;
}
.searchInput {
  height: 40px;
  width: 45%;
}
.searchInput select {
  border-radius: 10px 0px 0px 10px;
}
.searchInput input {
  outline: 0px;
}
.searchInput button {
  border-radius: 0px 10px 10px 0px;
}
.logo h1 {
  font-weight: bold;
}
.container {
  max-width: 1200px !important;
}
.menu {
  font-weight: bold;
}
.todayDeals {
  font-weight: normal;
}
@media (max-width: 950px) {
  .menu {
    font-size: 12px;
  }
  .navbarHead ul li a h6 {
    font-size: 12px;
  }
}
@media (max-width: 750px) {
  .searchInput input {
    border-radius: 10px;
    width: 90%;
  }
  .cart {
    font-size: 12px;
  }
}
.myAccountHover {
  display: none;
  position: absolute;
  width: 250px;
  color: black;
  top: 75%;
  right: 12%;
  text-align: center;
  padding-top: 15px;
  z-index: 1000;
}
.myAccountHoverBody {
  background: white;
  padding: 20px 15px 30px 15px ;
  border-radius: 10px;
}
.myAccountHover button:first-of-type {
  width: 75%;
  background: darkslateblue;
  padding: 7px;
  color: white;
  border: 0px;
  border-radius: 10px;
  margin: 5px 0px;
}
.myAccountHover button {
  width: 75%;
  background: darkslateblue;
  padding: 7px;
  color: white;
  border: 0px;
  border-radius: 10px;
  margin: 10px 0px;
}
.myAccountHover button h6 {
  font-weight: bold;
}
.myAccountHover div:nth-child(2) h6 {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background: white;
  z-index: 1;
  width: 40%;
}
.myAccountHover div:nth-child(3) svg {
  font-size: 50px;
  cursor: pointer;
  margin: 0px 10px;
}
.myAccount:hover .myAccountHover {
  display: block;
}
.myAccount:hover .mt-1 {
  transform: rotate(180deg);
}
.allCategories ul {
  display: none;
  list-style: none;
  background: white;
  color: black;
  padding: 20px;
  border-radius: 5px;
  position: absolute;
  left: 3%;
}
.allCategories ul li {
  padding: 10px 0px;
}
.allCategories:hover ul {
  display: block;
}
.allCategories:hover svg {
  transform: rotate(180deg);
}
.allCategories ul li:hover {
  background-color: bisque;
  padding: 10px;
  border-radius: 10px;
}
.allCategories ul li:hover a {
  color: brown;
}
.allCategories ul li a {
  text-decoration: none;
  color: black;
}
