body{
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    font-family: 'Montserrat', sans-serif;
    }
    .bg-custom{
    background-image: linear-gradient(midnightblue, #2e86de);
    border-top-left-radius: 50% 20%;
    border-top-right-radius: 50% 20%;
    }
    .bg-bottom{
    background-color: #2e86de;
    }
    .pt-30{
    padding-top: 30px;
    }
    ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
    }
    h2{
    font-weight:700;
    font-size:30px;
    letter-spacing: 2px;
    }
    h3{
    font-weight:700;
    font-size: 26px;
    letter-spacing: 2px;
    }
    li{
    padding-top: 10px;
    }
    a{
    text-decoration: none;
    }
    a:hover{
    text-decoration: none;
    color:#ddd;
    }
    .footerLink{
        color:#fff
    }
    .footerLink:hover{
    text-decoration: none;
    color:#ddd;
    }
    .fa{
    font-size: 40px;
    padding-right: 15px;
    }
    .download{
    text-align: center; 
    padding-top: 20px;
    }.download .fa{
    padding-right: 25px;
    font-size: 50px;
    }
    @media(max-width: 992px){
    h3{
    font-size: 18px;
    }
    .p-5 {
    padding: 2rem!important;
    }
    }
    @media(max-width: 830px){
    .pt-60{
    padding-top: 60px;
    }
    .col-md-3{
    padding-right:8px;
    padding-left: 8px;
    }
    h3 {
    font-size: 16px;
    }
    }
    @media(max-width: 768px){
    .col-md-3{
    text-align:center;
    }
    h3{
    font-size: 40px;
    }
    .input-group {
    margin-top:  1rem!important;
    }
    .pt-60{
    padding-top: 30px;
    }
    }
    @media(max-width: 475px){
    h3{
    font-size: 30px;
    padding-top: 30px;
    }
    .mobile-none{
    display:none;
    }
}